
.card{
    width: 100%;
    margin: 20px;
    border-radius: 6px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0px 1px 10px rgba(0,0,0,0.2);
    cursor: default;
    transition: all 400ms ease;
}
.card:hover{
    box-shadow: 5px 5px 20px rgba(0,0,0,0.4);
    transform: translateY(-3%);
}
.img{
    width: 100%;
    height: 210px;
}
.content{
    padding: 15px;
    text-align: center;
}
.p{
    line-height:1.5;
    color: #6a6a6a;
}
.h3{
    font-weight: 400;
    margin-bottom: 15px;
}

.link{
    display: inline-block;
    padding: 10px;
    margin-top: 10px;
    color: #2fb4cc;
    border: 1px solid #2fb4cc;
    border-radius: 4px;
    transition: all 400ms ease;
}
.link:hover{
    background: #2fb4cc;
    color: #fff;
}